import { should_never_happen } from "../../lib/src/should";
import { save_get } from "./lib/save";
import { app_canvas_setup, app_create, app_viewport_create } from "./lib/app";
import { hud_create } from "./lib/hud";
import { game_init } from "./lib/game";
import { FullMissionSchema, BundlesListSchema } from "./lib/schemas";
import * as v from 'valibot';
import { modal_show_bundle_select } from "./lib/modal";
import type { Save } from "./lib/types/save";

const VERSION = "0.0.1";

async function main() {
  const root_element = document.getElementById("app");
  if (!root_element) {
    should_never_happen("Root Element Not Found!");
    return;
  }
  let current_save = save_get(VERSION);
  if (!current_save.bundle) {
    const bundles_data = await (await fetch(`bundles/bundles.json`)).json();
    const validated_bundles = v.parse(BundlesListSchema, bundles_data);
    modal_show_bundle_select(validated_bundles.bundles, async (selected_id: string) => {
      const bundle_url = `bundles/${selected_id}/bundle.json`;
      const bundle_config = await (await fetch(bundle_url)).json();
      const first_mission = bundle_config.missions[0];
      if (!first_mission) {
        should_never_happen("No missions in bundle");
        return;
      }
      const new_save: Save = { version: VERSION, bundle: selected_id, mission: first_mission };
      localStorage.setItem('jdefense', JSON.stringify(new_save));
      location.reload();
    });
    return;
  }
  const bundle_url = `bundles/${current_save.bundle}/bundle.json`;
  const bundle_config = await (await fetch(bundle_url)).json();
  if (!bundle_config.missions.includes(current_save.mission)) {
    current_save.mission = bundle_config.missions[0] || '';
    localStorage.setItem('jdefense', JSON.stringify(current_save));
  }
  const mission_url = `bundles/${current_save.bundle}/${current_save.mission}`;
  const mission_data = await (await fetch(mission_url)).json();
  const validated_data = v.parse(FullMissionSchema, mission_data);
  const { enemies, towers, waves, mission } = validated_data;
  const hud = hud_create();
  for (const h of hud) {
    document.body.appendChild(h);
  }
  const app = await app_create();
  const canvas = app_canvas_setup(app);
  root_element.appendChild(canvas);
  const viewport = await app_viewport_create(app);
  app.stage.addChild(viewport);
  game_init({ enemies, towers, waves, mission });
}

main();