// src/lib/schemas.ts

import * as v from 'valibot';

export const NumberSchema = v.number();
export const StringSchema = v.string();
export const OptionalStringSchema = v.optional(v.string());

export const PathPointSchema = v.object({
  x: NumberSchema,
  y: NumberSchema,
});

export const ResistancesSchema = v.record(StringSchema, NumberSchema);

export const EnemySchema = v.object({
  health: NumberSchema,
  speed: NumberSchema,
  value: NumberSchema,
  resistances: ResistancesSchema,
});

export const EnemiesSchema = v.record(StringSchema, EnemySchema);

export const UpgradeEffectSchema = v.object({
  damage: v.optional(NumberSchema),
  range: v.optional(NumberSchema),
  fire_rate: v.optional(NumberSchema),
});

export const UpgradeSchema = v.object({
  name: StringSchema,
  display_name: StringSchema,
  cost: NumberSchema,
  effect: UpgradeEffectSchema,
});

export const TowerSchema = v.object({
  name: StringSchema,
  cost: NumberSchema,
  damage: NumberSchema,
  range: NumberSchema,
  fire_rate: NumberSchema,
  damage_type: StringSchema,
  color: NumberSchema,
  texture: OptionalStringSchema,
  upgrades: v.array(UpgradeSchema),
  hotkey: OptionalStringSchema,
});

export const TowersSchema = v.record(StringSchema, TowerSchema);

export const WaveEnemySchema = v.object({
  type: StringSchema,
  count: NumberSchema,
  spawn_delay: NumberSchema,
});

export const WaveSchema = v.object({
  number: NumberSchema,
  enemies: v.array(WaveEnemySchema),
});

export const WavesSchema = v.object({
  waves: v.array(WaveSchema),
});

export const MissionSoundsSchema = v.object({
  tower_build: StringSchema,
  enemy_spawn: StringSchema,
  damage_taken: StringSchema,
});

export const MissionSchema = v.object({
  name: StringSchema,
  description: StringSchema,
  map_texture: StringSchema,
  background_music: StringSchema,
  victory_sound: StringSchema,
  defeat_sound: StringSchema,
  starting_gold: NumberSchema,
  starting_lives: NumberSchema,
  wave_interval: NumberSchema,
  difficulty: v.union([v.literal('easy'), v.literal('medium'), v.literal('hard')]),
  total_waves: NumberSchema,
  sounds: MissionSoundsSchema,
  enemy_path: v.array(PathPointSchema),
  initial_pause: NumberSchema,
});

export const FullMissionSchema = v.object({
  mission: MissionSchema,
  enemies: EnemiesSchema,
  towers: TowersSchema,
  waves: WavesSchema,
});

export const BundleSchema = v.object({
  name: StringSchema,
  missions: v.array(StringSchema),
});

export const BundlesListSchema = v.object({
  bundles: v.array(v.object({
    id: StringSchema,
    name: StringSchema,
  })),
});