// containers.ts
// src/lib/containers.ts
import * as PIXI from 'pixi.js';
import { Viewport } from 'pixi-viewport';
export let containers_path: PIXI.Container | null = null;
export let containers_enemy: PIXI.Container | null = null;
export let containers_tower: PIXI.Container | null = null;
export let containers_projectile: PIXI.Container | null = null;
export function containers_init(viewport: Viewport): void {
  containers_path = new PIXI.Container();
  containers_enemy = new PIXI.Container();
  containers_tower = new PIXI.Container();
  containers_projectile = new PIXI.Container();
  viewport.addChild(containers_path);
  viewport.addChild(containers_enemy);
  viewport.addChild(containers_tower);
  viewport.addChild(containers_projectile);
}