// path.ts
// src/lib/path.ts
import { should_never_happen } from "../../../lib/src/should";
// TYPE IMPORTS
import type { PathConfig, PathDirection, PathPoint } from "./types/path";
// Generate a winding path from left to right across the screen
export function path_generate(points: PathPoint[]): PathConfig {
  const path_total_length = path_calculate_length(points);
  return {
    points,
    total_length: path_total_length,
  };
}
// Calculate total path length for movement calculations
function path_calculate_length(points: PathPoint[]): number {
  let total_length = 0;
  for (let i = 0; i < points.length - 1; i++) {
    const next_point = points[i + 1];
    const current_point = points[i];
    if (!next_point || !current_point) continue;
    const dx = next_point.x - current_point.x;
    const dy = next_point.y - current_point.y;
    total_length += Math.sqrt(dx * dx + dy * dy);
  }
  return total_length;
}
// Get position along path based on progress (0 to 1)
export function path_get_position(config: PathConfig, progress: number): PathPoint {
  const initial_point = config.points[0];
  const last_point = config.points[config.points.length - 1];
  if (!initial_point || !last_point) {
    should_never_happen("Invalid path config");
    return { x: 0, y: 0 };
  }
  if (progress <= 0) return { ...initial_point };
  if (progress >= 1) return { ...last_point };
  const target_distance = progress * config.total_length;
  let accumulated_distance = 0;
  for (let i = 0; i < config.points.length - 1; i++) {
    const start = config.points[i];
    const end = config.points[i + 1];
    if (!start || !end) continue;
    const dx = end.x - start.x;
    const dy = end.y - start.y;
    const segment_length = Math.sqrt(dx * dx + dy * dy);
    if (accumulated_distance + segment_length >= target_distance) {
      const segment_progress = (target_distance - accumulated_distance) / segment_length;
      return {
        x: start.x + dx * segment_progress,
        y: start.y + dy * segment_progress,
      };
    }
    accumulated_distance += segment_length;
  }
  return { ...last_point };
}
// Calculate distance between two points
export function path_distance(p1: PathPoint, p2: PathPoint): number {
  const dx = p2.x - p1.x;
  const dy = p2.y - p1.y;
  return Math.sqrt(dx * dx + dy * dy);
}
export function path_get_direction(config: PathConfig, progress: number): PathDirection {
  const target_distance = progress * config.total_length;
  let accumulated = 0;
  for (let i = 0; i < config.points.length - 1; i++) {
    const start = config.points[i];
    const end = config.points[i + 1];
    if (!start || !end) continue;
    const dx = end.x - start.x;
    const dy = end.y - start.y;
    const seg_len = Math.sqrt(dx * dx + dy * dy);
    if (accumulated + seg_len >= target_distance) {
      return { dx, dy }; // Current segment direction
    }
    accumulated += seg_len;
  }
  return { dx: 0, dy: 0 }; // At end or invalid
}
export function path_is_position_on_path(pos: PathPoint, config: PathConfig, buffer: number = 15): boolean {
  for (let i = 0; i < config.points.length - 1; i++) {
    const a = config.points[i];
    const b = config.points[i + 1];
    if (!a || !b) continue;
    const dist = path_distance_to_segment(pos, a, b);
    if (dist < buffer) return true;
  }
  return false;
}
function path_distance_to_segment(p: PathPoint, a: PathPoint, b: PathPoint): number {
  const l2 = path_distance(a, b) ** 2;
  if (l2 === 0) return path_distance(p, a);
  let t = ((p.x - a.x) * (b.x - a.x) + (p.y - a.y) * (b.y - a.y)) / l2;
  t = Math.max(0, Math.min(1, t));
  const proj = { x: a.x + t * (b.x - a.x), y: a.y + t * (b.y - a.y) };
  return path_distance(p, proj);
}