import * as PIXI from 'pixi.js';
import { addEntity, addComponent } from 'bitecs';
import { containers_tower } from './containers';
import { components } from './components';
import { graphicsByEntity, entityTypes, towerUpgrades, towerRangeGraphics } from './ecs_maps';
import { should_never_happen } from '../../../lib/src/should';
import { game_tooltip_show, game_tooltip_hide, game_state, game_elements } from './game';
import type { GameElements } from './types/game';
import type { CfgTower, CfgUpgrade } from './types/cfg';

const tower_size = 20;

const { Position, Tower, Cooldown } = components;

export function tower_create(
  world: any,
  type: string,
  pos: { x: number; y: number },
  elements: GameElements
): number {
  const config = elements.towers[type];
  if (!config) {
    should_never_happen(`Invalid tower type: ${type}`);
    return -1;
  }
  const eid = addEntity(world);
  addComponent(world, Tower, eid);
  addComponent(world, Position, eid);
  addComponent(world, Cooldown, eid);
  Position.x[eid] = pos.x;
  Position.y[eid] = pos.y;
  Cooldown.timer[eid] = 0;
  const graphics = new PIXI.Container();
  const body = new PIXI.Graphics().rect(-tower_size / 2, -tower_size / 2, tower_size, tower_size).fill(config.color);
  graphics.addChild(body);
  graphics.position.set(pos.x, pos.y);
  graphics.eventMode = 'static';
  graphics.on('pointerover', (e: PIXI.FederatedPointerEvent) => {
    const native = e.nativeEvent as PointerEvent;
    tower_show_range(eid, true);
    game_tooltip_show(eid, native.clientX, native.clientY);
  });
  graphics.on('pointerout', () => {
    if (game_state.selected_existing_tower !== eid) {
      tower_show_range(eid, false);
    }
    game_tooltip_hide();
  });
  if (containers_tower) {
    containers_tower.addChild(graphics);
  }
  graphicsByEntity.set(eid, graphics);
  entityTypes.set(eid, type);
  towerUpgrades.set(eid, []);
  const range_graphics = new PIXI.Graphics();
  graphics.addChild(range_graphics);
  towerRangeGraphics.set(eid, range_graphics);
  return eid;
}
export function tower_show_range(eid: number, show: boolean): void {
  if (!game_elements) return;
  const type = entityTypes.get(eid);
  if (!type) return;
  const config = game_elements.towers[type];
  if (!config) return;
  const effective_range = tower_get_effective_range(eid, config);
  const range_graphics = towerRangeGraphics.get(eid);
  if (!range_graphics) return;
  if (show) {
    range_graphics.clear();
    range_graphics.circle(0, 0, effective_range);
    range_graphics.stroke({ width: 2, color: 0xffffff, alpha: 0.5 });
  }
  range_graphics.visible = show;
}
export function tower_get_effective_damage(eid: number, config: CfgTower): number {
  let damage = config.damage;
  const upgrades = towerUpgrades.get(eid) || [];
  for (const upgrade of upgrades) {
    const up = config.upgrades.find((u: CfgUpgrade) => u.name === upgrade);
    if (up?.effect.damage) damage += up.effect.damage;
  }
  return damage;
}
export function tower_get_effective_range(eid: number, config: CfgTower): number {
  let range = config.range;
  const upgrades = towerUpgrades.get(eid) || [];
  for (const upgrade of upgrades) {
    const up = config.upgrades.find((u: CfgUpgrade) => u.name === upgrade);
    if (up?.effect.range) range += up.effect.range;
  }
  return range;
}
export function tower_get_effective_fire_rate(eid: number, config: CfgTower): number {
  let fire_rate = config.fire_rate;
  const upgrades = towerUpgrades.get(eid) || [];
  for (const upgrade of upgrades) {
    const up = config.upgrades.find((u: CfgUpgrade) => u.name === upgrade);
    if (up?.effect.fire_rate) fire_rate += up.effect.fire_rate;
  }
  return fire_rate;
}
export function tower_add_upgrade_indicator(eid: number, upgrade_name: string): void {
  const graphics = graphicsByEntity.get(eid);
  if (!graphics) return;
  const indicator = new PIXI.Graphics().circle(0, 0, 5);
  let color = 0xffffff;
  if (upgrade_name.includes('damage')) color = 0xff0000;
  else if (upgrade_name.includes('range')) color = 0x00ff00;
  else if (upgrade_name.includes('fire_rate')) color = 0x0000ff;
  indicator.fill(color);
  const upgrades = towerUpgrades.get(eid) || [];
  const index = upgrades.length - 1;
  indicator.position.set(-10 + index * 10, -15);
  graphics.addChild(indicator);
}