// systems.ts
import * as PIXI from 'pixi.js';
import { defineQuery, hasComponent, removeEntity, addComponent, addEntity } from 'bitecs';
import { path_get_position, path_get_direction } from './path';
import { components } from './components';
import { graphicsByEntity, entityTypes, projectileDamageTypes, towerUpgrades } from './ecs_maps';
import { projectile_speed, projectile_radius } from './projectile';
import { tower_get_effective_damage, tower_get_effective_fire_rate, tower_get_effective_range } from './tower';
import { containers_projectile } from './containers';
import type { PathConfig } from './types/path';
import type { GameElements } from './types/game';

const { Position, Health, Progress, Cooldown, Projectile, Enemy, Tower, Destroy } = components;

export const enemyQuery = defineQuery([Enemy, Position, Progress, Health]);
export const towerQuery = defineQuery([Tower, Position, Cooldown]);
export const projectileQuery = defineQuery([Projectile, Position]);
export const destroyQuery = defineQuery([Destroy]);

type GameEvent = { type: 'enemy_killed'; eid: number } | { type: 'enemy_reached'; eid: number };

export function systems_enemy_movement(
  world: any,
  delta_seconds: number,
  elements: GameElements,
  path_config: PathConfig,
  events: GameEvent[]
): void {
  const ents = enemyQuery(world);
  for (let i = 0; i < ents.length; i++) {
    const eid = ents[i];
    if (eid && Health.value[eid]! <= 0) {
      events.push({ type: 'enemy_killed', eid });
      addComponent(world, Destroy, eid);
      continue;
    }
    const type = eid ? entityTypes.get(eid) : undefined;
    if (!type) continue;
    const config = elements.enemies[type];
    if (!config) continue;
    if (eid) {
      Progress.value[eid]! += (config.speed * delta_seconds) / path_config.total_length;
      if (Progress.value[eid]! >= 1) {
        events.push({ type: 'enemy_reached', eid });
        addComponent(world, Destroy, eid);
        continue;
      }
    }
    if (!eid) continue;
    const pos = path_get_position(path_config, Progress.value[eid]!);
    Position.x[eid]! = pos.x;
    Position.y[eid]! = pos.y;
    const graphics = graphicsByEntity.get(eid);
    if (!graphics) continue;
    graphics.position.set(pos.x, pos.y);
    const dir = path_get_direction(path_config, Progress.value[eid]!);
    graphics.rotation = Math.atan2(dir.dy, dir.dx);
  }
}

export function systems_tower_attack(
  world: any,
  delta_seconds: number,
  elements: GameElements,
  enemy_eids: number[]
): void {
  const ents = towerQuery(world);
  for (let i = 0; i < ents.length; i++) {
    const eid = ents[i];
    if (!eid) continue;
    Cooldown.timer[eid]! -= delta_seconds;
    if (Cooldown.timer[eid]! > 0) continue;
    const type = entityTypes.get(eid);
    if (!type) continue;
    const config = elements.towers[type];
    if (!config) continue;
    const effective_range = tower_get_effective_range(eid, config);
    let closest_enemy: number | null = null;
    let min_dist = Infinity;
    for (const enemy_eid of enemy_eids) {
      if (!hasComponent(world, Position, enemy_eid)) continue;
      const dx = Position.x[enemy_eid]! - Position.x[eid]!;
      const dy = Position.y[enemy_eid]! - Position.y[eid]!;
      const dist = Math.sqrt(dx * dx + dy * dy);
      if (dist < effective_range && dist < min_dist) {
        min_dist = dist;
        closest_enemy = enemy_eid;
      }
    }
    if (closest_enemy !== null) {
      systems_projectile_create(world, eid, closest_enemy, elements);
      const effective_fire_rate = tower_get_effective_fire_rate(eid, config);
      Cooldown.timer[eid]! = 1 / effective_fire_rate;
    }
  }
}

export function systems_projectile_update(
  world: any,
  delta_seconds: number,
  elements: GameElements
): void {
  const ents = projectileQuery(world);
  for (let i = 0; i < ents.length; i++) {
    const eid = ents[i];
    if (!eid) continue;
    const target = Projectile.target[eid]!;
    if (!hasComponent(world, Position, target) || !hasComponent(world, Health, target)) {
      addComponent(world, Destroy, eid);
      continue;
    }
    const dx = Position.x[target]! - Position.x[eid]!;
    const dy = Position.y[target]! - Position.y[eid]!;
    const dist = Math.sqrt(dx * dx + dy * dy);
    if (dist < projectile_radius) {
      systems_projectile_apply_damage(world, eid, target, elements);
      addComponent(world, Destroy, eid);
      continue;
    }
    const move_dist = projectile_speed * delta_seconds;
    const ratio = move_dist / dist;
    if (ratio > 1) {
      Position.x[eid]! = Position.x[target]!;
      Position.y[eid]! = Position.y[target]!;
    } else {
      Position.x[eid]! += dx * ratio;
      Position.y[eid]! += dy * ratio;
    }
    const graphics = graphicsByEntity.get(eid);
    if (!graphics) continue;
    graphics.position.set(Position.x[eid]!, Position.y[eid]!);
  }
}

function systems_projectile_create(
  world: any,
  tower_eid: number,
  target_eid: number,
  elements: GameElements
): void {
  const type = entityTypes.get(tower_eid);
  if (!type) return;
  const config = elements.towers[type];
  if (!config) return;
  const damage = tower_get_effective_damage(tower_eid, config);
  const damage_type = config.damage_type;
  const eid = addEntity(world);
  addComponent(world, Projectile, eid);
  addComponent(world, Position, eid);
  Position.x[eid]! = Position.x[tower_eid]!;
  Position.y[eid]! = Position.y[tower_eid]!;
  Projectile.target[eid]! = target_eid;
  Projectile.damage[eid]! = damage;
  const graphics = new PIXI.Container();
  const body = new PIXI.Graphics().circle(0, 0, projectile_radius);
  let color = 0xffffff;
  switch (damage_type) {
    case 'physical': color = 0x808080; break;
    case 'fire': color = 0xff0000; break;
    case 'ice': color = 0x00ffff; break;
    case 'poison': color = 0x00ff00; break;
    case 'holy': color = 0xffff00; break;
  }
  body.fill(color);
  graphics.addChild(body);
  graphics.position.set(Position.x[eid]!, Position.y[eid]!);
  if (containers_projectile) {
    containers_projectile.addChild(graphics);
  }
  graphicsByEntity.set(eid, graphics);
  projectileDamageTypes.set(eid, damage_type);
}

function systems_projectile_apply_damage(
  _: any,
  proj_eid: number,
  target_eid: number,
  elements: GameElements
): void {
  const target_type = entityTypes.get(target_eid);
  if (!target_type) return;
  const config = elements.enemies[target_type];
  if (!config) return;
  const damage_type = projectileDamageTypes.get(proj_eid);
  if (!damage_type) return;
  const resistance = config.resistances[damage_type] ?? 1.0;
  const effective_damage = Projectile.damage[proj_eid]! * resistance;
  Health.value[target_eid]! -= effective_damage;
  if (Health.value[target_eid]! < 0) Health.value[target_eid]! = 0;
}

export function systems_cleanup(world: any): void {
  const ents = destroyQuery(world);
  for (let i = 0; i < ents.length; i++) {
    const eid = ents[i];
    if (!eid) continue;
    const graphics = graphicsByEntity.get(eid);
    if (graphics) {
      if (graphics.parent) graphics.parent.removeChild(graphics);
      graphics.destroy({ children: true });
      graphicsByEntity.delete(eid);
    }
    entityTypes.delete(eid);
    projectileDamageTypes.delete(eid);
    towerUpgrades.delete(eid);
    removeEntity(world, eid);
  }
}