import * as PIXI from 'pixi.js';
import { addEntity, addComponent } from 'bitecs';
import { containers_enemy } from './containers';
import { path_get_position } from './path';
import { components } from './components';
import { graphicsByEntity, entityTypes } from './ecs_maps';
import { should_never_happen } from '../../../lib/src/should';
import type { PathConfig } from './types/path';
import type { GameElements } from './types/game';

const enemy_radius = 10;

const { Position, Enemy, Progress, Health } = components;

function get_color_for_enemy_type(type: string): number {
  switch (type) {
    case 'basic': return 0x00ff00;
    case 'fast': return 0xffff00;
    case 'armored': return 0x808080;
    case 'flying': return 0x00ffff;
    case 'boss': return 0xff00ff;
    case 'undead': return 0x800080;
    default: return 0xff0000;
  }
}

export function enemy_create(
  world: any,
  enemy_type: string,
  elements: GameElements,
  path_config: PathConfig
): number {
  const config = elements.enemies[enemy_type];
  if (!config) {
    should_never_happen(`Invalid enemy type: ${enemy_type}`);
    return -1;
  }
  const eid = addEntity(world);
  addComponent(world, Enemy, eid);
  addComponent(world, Position, eid);
  addComponent(world, Health, eid);
  addComponent(world, Progress, eid);
  Progress.value[eid] = 0;
  Health.value[eid] = config.health;
  const start_pos = path_get_position(path_config, 0);
  Position.x[eid] = start_pos.x;
  Position.y[eid] = start_pos.y;
  const graphics = new PIXI.Container();
  const body = new PIXI.Graphics().circle(0, 0, enemy_radius);
  body.stroke({ width: 1, color: 0x000000 });
  body.fill(get_color_for_enemy_type(enemy_type));
  graphics.addChild(body);
  graphics.position.set(start_pos.x, start_pos.y);
  if (containers_enemy) {
    containers_enemy.addChild(graphics);
  }
  graphicsByEntity.set(eid, graphics);
  entityTypes.set(eid, enemy_type);
  return eid;
}