// map.ts
// src/lib/map.ts
import * as PIXI from 'pixi.js';
import { should_never_happen } from '../../../lib/src/should';
import { containers_path } from './containers';
import type { PathConfig } from './types/path';
export function map_draw_path(path_config: PathConfig): void {
  const points = path_config.points;
  const starting_point = points[0];
  if (!starting_point) return;
  const path_graphics = new PIXI.Graphics();
  path_graphics.setStrokeStyle({
    width: 30,
    color: 0x654321,
    alpha: 0.75
  });
  path_graphics.moveTo(starting_point.x, starting_point.y);
  for (let i = 1; i < points.length; i++) {
    const current_point = points[i];
    if (!current_point) continue;
    path_graphics.lineTo(current_point.x, current_point.y);
  }
  path_graphics.stroke();
  if (!containers_path) {
    should_never_happen('Path container not found');
    return;
  }
  containers_path.addChild(path_graphics);
}